@tailwind base;
.ticket-reply-body a {
  @apply text-blue-600 underline break-all;
}

.ticket-conversation {
  @apply flex flex-col overflow-auto;
  height: calc(100vh - 180px);
}
@media (min-width: theme("screens.sm")) {
  .ticket-conversation {
    height: calc(100vh - 120px);
  }
}

@tailwind components;
.fc-day-grid-event {
  @apply px-2 py-0 mx-2 my-1 cursor-pointer text-sm select-none;
}

.fc-unthemed td.fc-today {
  @apply bg-gray-100;
}

.fc-day-number {
  @apply text-xs font-semibold m-2;
}

thead > tr > .fc-day-header {
  @apply text-xs uppercase font-semibold text-gray-600;
}

.fc-widget-header {
  @apply border-gray-200;
}

.fc-unthemed td {
  @apply border-gray-200;
}

.table-footer-bg {
  @apply bg-white;
}

.layout-bg {
  @apply bg-gray-100;
}

.max-height-wrap {
  maxheight: "200px";
}

.content-title {
  @apply text-2xl px-1 font-semibold text-gray-600 uppercase;
}

.content-title-icon {
  @apply flex-shrink-0 pt-1 h-7 w-7 text-gray-600;
}

.content-card-body {
  @apply bg-white shadow overflow-hidden rounded-lg;
}

.sidebar-icon-text-color {
  @apply text-blue-400;
}

.sidebar-bg {
  background: #00172c;
}

.card-footer {
  @apply bg-white text-right px-4 py-3;
}
.card-footer:sm {
  @apply px-6;
}

.block-link-icon {
  @apply w-20 h-20 p-3 text-black text-gray-500;
}

.form-description-text {
  @apply mt-1 text-xs leading-5 text-gray-500;
}

.form-description-head {
  @apply text-lg font-medium leading-6 capitalize text-gray-600;
}

.form-input {
  @apply w-full px-4 py-2 rounded-md border border-gray-300;
}

.form-label {
  @apply block text-sm font-medium leading-5 text-gray-700;
}

.router-link {
  @apply flex items-center px-4 py-3 transition ease-in-out duration-150 font-medium;
}
.router-link:focus {
  @apply outline-none bg-gray-600;
}
.router-link.router-link-desktop {
  @apply text-sm leading-5;
}
.router-link.router-link-mobile {
  @apply text-base leading-6;
}
.router-link.router-link-active, .router-link.router-link-exact-active {
  @apply text-white bg-gray-800;
}
.router-link:not(.router-link-active), .router-link:not(.router-link-exact-active) {
  @apply text-white;
}
.router-link:not(.router-link-active):hover, .router-link:not(.router-link-exact-active):hover {
  @apply text-white bg-gray-800;
}
.router-link:not(.router-link-active):focus, .router-link:not(.router-link-exact-active):focus {
  @apply text-white bg-gray-800;
}

.btn {
  @apply px-4 py-2 rounded-md border border-transparent leading-5 font-medium shadow;
}
.btn:focus {
  @apply outline-none;
}
.btn:disabled {
  @apply opacity-50 cursor-not-allowed;
}
.btn.btn-white {
  @apply border-gray-300 bg-white text-gray-700;
}
.btn.btn-white:not(:disabled):hover {
  @apply text-gray-500;
}
.btn.btn-white:not(:disabled):focus {
  @apply border-blue-300;
}
.btn.btn-blue {
  @apply bg-blue-500 text-white;
}
.btn.btn-blue:not(:disabled):hover {
  @apply bg-blue-400;
}
.btn.btn-blue:not(:disabled):active {
  @apply bg-blue-600;
}
.btn.btn-blue:not(:disabled):focus {
  @apply border-blue-600;
}
.btn.btn-app {
  @apply bg-blue-500 text-white;
}
.btn.btn-app:not(:disabled):hover {
  @apply bg-blue-400;
}
.btn.btn-app:not(:disabled):active {
  @apply bg-blue-600;
}
.btn.btn-app:not(:disabled):focus {
  @apply border-blue-600;
}
.btn.btn-green {
  @apply bg-green-500 text-white;
}
.btn.btn-green:not(:disabled):hover {
  @apply bg-green-400;
}
.btn.btn-green:not(:disabled):active {
  @apply bg-green-600;
}
.btn.btn-green:not(:disabled):focus {
  @apply border-green-600;
}
.btn.btn-yellow {
  @apply bg-yellow-400 text-white;
}
.btn.btn-yellow:not(:disabled):hover {
  @apply bg-yellow-300;
}
.btn.btn-yellow:not(:disabled):active {
  @apply bg-yellow-500;
}
.btn.btn-yellow:not(:disabled):focus {
  @apply border-yellow-500;
}
.btn.btn-purple {
  @apply bg-purple-500 text-white;
}
.btn.btn-purple:not(:disabled):hover {
  @apply bg-purple-400;
}
.btn.btn-purple:not(:disabled):active {
  @apply bg-purple-600;
}
.btn.btn-purple:not(:disabled):focus {
  @apply border-purple-600;
}
.btn.btn-red {
  @apply bg-red-500 text-white;
}
.btn.btn-red:not(:disabled):hover {
  @apply bg-red-400;
}
.btn.btn-red:not(:disabled):active {
  @apply bg-red-600;
}
.btn.btn-red:not(:disabled):focus {
  @apply border-red-600;
}
.btn.btn-secondary {
  @apply bg-gray-500 text-white;
}
.btn.btn-secondary:not(:disabled):hover {
  @apply bg-gray-400;
}
.btn.btn-secondary:not(:disabled):active {
  @apply bg-gray-600;
}
.btn.btn-secondary:not(:disabled):focus {
  @apply border-gray-600;
}

.pagination-link {
  @apply relative rounded-md inline-flex items-center px-4 py-2 border border-gray-300 text-sm leading-5 font-medium text-gray-700 bg-white transition duration-150 ease-in-out;
}
.pagination-link:not(:disabled):hover {
  @apply text-gray-500;
}
.pagination-link:not(:disabled):focus {
  @apply outline-none border-blue-300;
}
.pagination-link:not(:disabled):active {
  @apply bg-gray-100 text-gray-700;
}

.velmld-overlay {
  z-index: 35 !important;
}

@tailwind utilities;
.z-1500 {
  z-index: 1500;
}

@media print {
  .grecaptcha-badge {
    visibility: hidden !important;
  }
}
.tox-tinymce,
.tox-toolbar__group {
  border-color: #cbd5e0 !important;
}

.tox-toolbar__primary {
  background: url("data:image/svg+xml;charset=utf8,%3Csvg height='39px' viewBox='0 0 40 39px' width='40' xmlns='http://www.w3.org/2000/svg'%3E%3Crect x='0' y='38px' width='100' height='1' fill='%23cbd5e0'/%3E%3C/svg%3E") left 0 top 0 #fff !important;
}